  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

/* My styles */
body {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  /* For drag and drop. When a ship is dragged, a div is created, and it must be put somewhere. So it's put outside the visible area. */
  overflow: hidden;
}
button:hover {
  cursor: pointer;
}
form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  & button {
    align-self: center;
    padding: 0.2rem 3rem;
  }
}
.players-form {
  display: flex;
  gap: 3rem;
}
.player-form {
  display: flex;
  flex-direction: column;
  & h2 {
    align-self: center;
  }
}
.boards {
  display: flex;
  gap: 10rem;
  & > div {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.player-one,
.player-two {
  position: relative;
}
.player-one .board {
  display: flex;
  flex-direction: column;
}
.player-two .board {
  display: none;
  flex-direction: column;
}
.player-one .board-enemy {
  display: none;
  flex-direction: column;
}
.player-two .board-enemy {
  display: flex;
  flex-direction: column;
}
.board-enemy .square:hover {
  cursor: pointer;
}
.row,
.cols {
  height: 3rem;
  flex-shrink: 0;
  display: flex;
}
.square {
  width: 3rem;
  height: 3rem;
  flex-shrink: 0;
  outline: 1px solid black;
}
.num,
.letter,
.empty {
  width: 3rem;
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}
.turn-wrapper {
  font-size: 1.5rem;
}
.square.occupied,
.square.available {
  background-color: black;
}
.square.hit {
  background-color: red;
}
.square.missed {
  background-color: blue;
}
.square.sunk {
  background: grey;
}
.board,
.board-enemy {
  position: relative;
}
#hand-over-one[open],
#hand-over-two[open],
#hand-over-one-enemy[open],
#hand-over-two-enemy[open],
#ready-one[open],
#ready-two[open] {
  width: calc(100% - 3rem);
  height: calc(100% - 3rem);
  top: 3rem;
  left: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
